@import url('https://fonts.googleapis.com/css?family=Open+Sans|Overpass:600|Source+Sans+Pro:400,700&display=swap');

.mainFooter {
  background-color: #283593;
  padding-top: 17px;
  padding-bottom: 21px;
  display: flex;
  flex-direction: column;
  align-items: center;

  &__container {
    width: 1140px;
    padding: 0 15px;
  }

  &__menu {
    display: flex;
    justify-content: space-between;
    padding-bottom: 30px;
    border-bottom: 1px solid #5674b9;

    &-item {
      color: #ffffff;
      padding-top: 14px;
    }

    &-logo {
      margin-top: 11px;

      &-item {
        width: 134px;
        height: 24px;
        cursor: pointer;
        fill: #ffffff;
        transition: 0.2s fill ease-in-out;

        &:hover {
          //fill: #ef6c00;
        }
      }
    }
  }

  &__extended-menu {
    margin-top: 21px;
    display: flex;
    justify-content: space-between;
  }

  &__auxiliary-menu {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 24px;

    &-copyright {
      color: #ffffff;
      font-family: "Source Sans Pro";
      font-size: 12px;
      padding-bottom: 4px;
    }

    &-languages {
      display: flex;
      padding-left: 35px;

      &-item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 48px;
        height: 48px;
        font-family: "Source Sans Pro";
        font-size: 14px;
        font-weight: 700;
        color: #ffffff;
        cursor: pointer;
        transition: 0.2s color ease-in-out;

        &:first-child {
          margin-left: 0;
        }

        &:hover {
          color: #ef6c00;
        }

        &-value {
          display: flex;
          justify-content: center;
          align-items: center;
          width: 26px;
          height: 26px;
          border-radius: 6px;
        }

        &.active {
          cursor: default;
        }

        &.active &-value {
          color: #283593;
          background-color: #ffffff;
          cursor: default;
        }
      }
    }

    &-social-networks {
      display: flex;
      margin-right: -11px;

      &-item {

      }
    }
  }
}

@media (max-width: 1170px) {
  .mainFooter {
    padding-top: 19px;
    padding-bottom: 17px;
    &__container {
      width: 929px;
    }

    &__auxiliary-menu {
      align-items: center;

      &-copyright {
        padding-top: 11px;
      }

      &-languages {
        //padding-left: 26px;
        padding-bottom: 2px;

        &-item {
          //margin-left: 23px;
        }
      }
    }
  }
}

@media (max-width: 960px) {
  .mainFooter {
    padding-top: 19px;
    padding-bottom: 22px;

    &__container {
      width: 721px;
    }

    &-languages {

      &-item {

      }
    }

    &__menu {
      padding-bottom: 29px;
    }

    &__extended-menu {
      margin-top: 24px;
    }

    &__auxiliary-menu {
      align-items: flex-start;
      margin-top: 16px;

      &-copyright {
        padding-top: 23px;
      }

      &-social-networks {
        padding-top: 8px;
      }

      &-languages {
        padding-bottom: 0;
        padding-top: 8px;
      }
    }
  }
}

@media (max-width: 755px) {
  .mainFooter {
    padding-top: 20px;
    padding-bottom: 19px;

    &__container {
      width: 100vw;
      padding: 0 15px;
      box-sizing: border-box;
    }

    &__menu {
      flex-direction: column;
      align-items: center;
      padding-bottom: 32px;

      &-item {
      }
    }

    &__extended-menu {
      justify-content: center;
      margin-top: 22px;

      &-menu {
        display: none;
      }
    }

    &__auxiliary-menu {
      flex-direction: column;
      align-items: center;
      margin-top: 14px;

      &-copyright {
        order: 1;
        padding-top: 17px;
      }

      &-languages {
        order: 2;
        padding-left: 0;
        margin-top: 5px;

        &-item {

        }
      }

      &-social-networks {
        padding-top: 2px;
      }
    }
  }
}