.contacts__contact {
  display: grid;
  height: 25px;
  grid-template-columns: 45px 1fr;
  grid-template-rows: 25px;
  margin-top: 20px;
  margin-left: -10px;
  padding-top: 2px;
  box-sizing: border-box; }
  .contacts__contact path {
    -webkit-transition: 0.2s fill ease-in-out;
    transition: 0.2s fill ease-in-out; }
  .contacts__contact:hover .contacts__contact-value {
    color: #ef6c00; }
  .contacts__contact:hover path, .contacts__contact:hover polygon {
    fill: #ef6c00; }
  .contacts__contact:first-child {
    margin-top: 0; }
  .contacts__contact-icon {
    display: flex;
    align-items: center;
    justify-content: center; }
    .contacts__contact-icon.fix-mail {
