.extended-menu {
  padding-top: 4px;

  &__list {
    display: grid;
    grid-template-columns: 100px 100px 100px 100px;
    column-gap: 95px;
    row-gap: 14px;
  }

  &__item {
    display: flex;
    justify-content: flex-end;

    a {
      color: #ffffff;
      text-decoration: none;
      transition: 0.2s color ease-in-out;
      font-family: "Source Sans Pro";
      font-size: 16px;

      &:hover {
        color: #ef6c00;
      }
    }
  }
}

@media (max-width: 1170px) {
  .extended-menu {

    &__list {
      column-gap: 60px;
    }
  }
}

@media (max-width: 960px) {
  .extended-menu {

    &__list {
      column-gap: 25px;
    }
  }
}