.contacts {

  &__contact {
    display: grid;
    height: 25px;
    grid-template-columns: 45px 1fr;
    grid-template-rows: 25px;
    //column-gap: 9px;
    margin-top: 20px;
    margin-left: -10px;
    padding-top: 2px;
    box-sizing: border-box;

    path {
      transition: 0.2s fill ease-in-out;
    }

    &:hover {

      .contacts__contact-value {
        color: #ef6c00;
      }

      path, polygon {
        fill: #ef6c00;
      }
    }

    &:first-child {
      margin-top: 0;
    }

    &-icon {
      //margin-top: 5px;
      display: flex;
      align-items: center;
      justify-content: center;

      &.fix-mail {
        padding-top: 5px;
      }
    }

    &-value {
      display: flex;
      align-items: center;
      color: #ffffff;
      font-family: "Source Sans Pro";
      font-size: 20px;
      text-decoration: none;
      transition: 0.2s color ease-in-out;
    }
  }
}

@media (max-width: 960px) {
  .contacts {

    &__contact {
      display: block;
      padding-top: 5px;
      margin-top: 19px;
      margin-left: 0;

      &-value {
        font-size: 18px;
      }

      &-icon {
        display: none;
      }
    }
  }
}

@media (max-width: 755px) {
  .contacts {

    &__contact {
      margin-top: 10px;

      &-value {
        justify-content: center;
      }
    }
  }
}